.header {
  margin-top: 60px;
  text-align: center;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 80px;
  object-fit: cover;
}

@media (max-width: 768px) {
  .headerTitleSm {
    font-size: 20px;
    top: 15%;
  }

  .headerTitleLg {
    font-size: 60px;
    top: 20%;
    line-height: 70px;
  }
}
